.dark-bg {
    background-color: #1f1f1f;
}

.logos {
    width: 100px;
}

#react {
    animation: rotation 2s infinite linear;
}

#html, #css, #php, #js, #sql {
    animation: scale 2s infinite linear;
}

@keyframes scale {
    0% {
        scale: 1;
    }

    25% {
        scale: 1.05;
    }

    50% {
        scale: 1.1;
    }

    75% {
        scale: 1.05;
    }

    100% {
        scale: 1;
    }

    /* from {
        scale: 1;
    }

    to {
        scale: 1.1;
    } */
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(359deg);
    }
}