
.workshop-bg-1 {
    background-color: hsla(0,0%,100%,.9);
    color: #000;
    border-radius: 16px;
}
.workshop-bg-2 {
    background-color: #cf0a56;
    border-radius: 16px;
}
.workshop-bg-2 .custom-button {
    background: #000;
}
.workshop-bg-2 .custom-button:hover {
    background: rgb(24, 24, 24);
}

.custom-button, button {
    background-color: #cf0a56;
    border: none;
    color: #fff;
    font-weight: 800;
    border-radius: 6px;
    font-size: 18px;
}
.social-icon{
    font-size: 24px;
}