*{
    box-sizing: border-box;
    text-decoration: none !important;
    font-family: 'Quicksand', sans-serif;
}
body{
    background-color: #222;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
}
.section{
    height: auto;
}
h1{
    
    font-weight: 700;
    font-size: 90px;
    color: white;
    margin: 0;
    line-height: 85px;
}
p{
   color: white;
}
.w-100 {
    width: 100%!important;
}
img, svg {
    vertical-align: middle;
}
.social{
    font-size: 24px;
}
.custom-button {
    background-color: #cf0a56;
    border: none;
    color: #fff;
    font-weight: 800;
    border-radius: 6px;
    font-size: 18px;
}
.custom-button:hover {
    background-color: #d9115e;
    color: white;
}

@media screen and (max-width: 600px) {
    .social{
        justify-content: center;
    }
    .section{
        height: auto;
    }
}

@media screen and (max-width: 991px) {
    .social{
        justify-content: center;
    }
}